// action - state management
import * as actionTypes from './actions';

export const initialState = {
    appLogs: [
        {
            type: 'info',
            message: '\x1b[30mblack\x1b[37mwhite'
        }
    ],
    services: [],
    solution: {}
};

// ==============================|| COMMON REDUCER ||============================== //

const commonReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.ADD_APP_LOG:
            const newLogs = [...state.appLogs, action.payload];
            return {
                ...state,
                appLogs: newLogs
            };
        case actionTypes.SET_SERVICES:
            return {
                ...state,
                services: action.payload
            };
        case actionTypes.SET_SOLUTION:
            return {
                ...state,
                solution: { ...state.solution, ...action.payload }
            };
        default:
            return state;
    }
};

export default commonReducer;
