import { useDispatch, useSelector } from 'react-redux';
import useBus from 'use-bus';
import { ThemeProvider } from '@mui/material/styles';
import { CssBaseline, StyledEngineProvider } from '@mui/material';
import io from 'socket.io-client';
import { ADD_APP_LOG } from 'store/actions';

// routing
import Routes from 'routes';

// defaultTheme
import themes from 'themes';

// project imports
import NavigationScroll from 'layout/NavigationScroll';
import { useState, useEffect } from 'react';
const socket = io();
import { useSnackbar } from 'notistack';

// ==============================|| APP ||============================== //
const App = () => {
    const [isConnected, setIsConnected] = useState(socket.connected);
    const [lastPong, setLastPong] = useState(null);
    const dispatch = useDispatch();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    useBus('@@ui/wsemit', (action) => socket.emit('dispatch', action.payload), [socket]);

    useEffect(() => {
        socket.on('connect', () => {
            dispatch({ type: ADD_APP_LOG, payload: { text: '> Agent connected to CLI!' } });
            enqueueSnackbar('Agent connected to CLI', { preventDuplicate: true, variant: 'success' });
            setIsConnected(true);
        });

        socket.on('disconnect', () => {
            enqueueSnackbar('Agent disconnected to CLI', { preventDuplicate: true, variant: 'error' });
            setIsConnected(false);
        });

        socket.io.on('reconnect', (attempt) => {
            enqueueSnackbar('Agent try to connect to CLI ... #' + attempt, { preventDuplicate: true, variant: 'warn' });
        });

        socket.io.on('reconnect_attempt', (attempt) => {
            enqueueSnackbar('Agent try to connect to CLI ... #' + attempt, { preventDuplicate: true, variant: 'warn' });
        });

        socket.on('dispatch', (action) => {
            console.log('Action from CLI ', action);
            dispatch(action);
        });

        return () => {
            socket.off('connect');
            socket.off('disconnect');
            socket.off('pong');
        };
    }, []);

    const customization = useSelector((state) => state.customization);

    return (
        <StyledEngineProvider injectFirst>
            <ThemeProvider theme={themes(customization)}>
                <CssBaseline />
                <NavigationScroll>
                    <Routes />
                </NavigationScroll>
            </ThemeProvider>
        </StyledEngineProvider>
    );
};

export default App;
