import { createStore } from 'redux';
import reducer from './reducer';
import _ from 'lodash';

// ==============================|| REDUX - MAIN STORE ||============================== //

let initialState;
const defaultState = {
    common: {
        appLogs: [],
        services: [],
        solution: {},
        solution: {}
    }
};
if (window.__telar_init_sate) {
    initialState = _.merge(defaultState, window.__telar_init_sate);
}
const store = createStore(reducer, initialState);
const persister = 'Telar';

export { store, persister };
